import { animate, animation, query, sequence, stagger, style, transition, trigger } from '@angular/animations';

export const SidebarOpenAnimation = animation([
  style({ left: '-{{menuWidth}}' }),
  query('.form-group', [style({ transform: 'translateX(-{{menuWidth}})' })]),
  sequence([
    animate('200ms', style({ left: '0' })),
    query('.form-group', [stagger(50, [animate('{{animationStyle}}', style({ transform: 'none' }))])]),
  ]),
]);

export const SidebarCloseAnimation = animation([
  style({ left: '0' }),
  query('.form-group', [style({ transform: 'none' })]),
  sequence([
    query('.form-group', [
      stagger(-50, [animate('{{animationStyle}}', style({ transform: 'translateX(-{{menuWidth}})' }))]),
    ]),
    animate('200ms', style({ left: '-{{menuWidth}}' })),
  ]),
]);

export const slideInOutAnimation = [
  trigger('inOutAnimation', [
    transition(':enter', [style({ opacity: 0 }), animate('500ms ease-in', style({ opacity: 1 }))]),
    transition(':leave', [style({ opacity: 1 }), animate('300ms ease-out', style({ opacity: 0 }))]),
  ]),
];
