// eslint-disable-next-line @typescript-eslint/consistent-type-imports
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { IScorecards } from '../../score-metrics/interfaces/score-metric.interface';

@Injectable({
  providedIn: 'root',
})
export class AdvancedDriverService {
  private readonly apiEndPoint = environment.APIUrl + 'DriverBehavior/DriverScorecard/';

  constructor(private http: HttpClient) {}

  getDriverScoreCard(scoreCardId: number, from: String, to: string, tenantId: number) {
    return this.http.get(
      `${this.apiEndPoint}GetDriverScores?scoreCardId=${scoreCardId}&from=${from}&to=${to}&tenantId=${tenantId}`
    );
  }

  getTenants() {
    return this.http.get(`${this.apiEndPoint}GetTenants`);
  }

  getScorecards(tenantId: number): Observable<IScorecards[]> {
    return this.http.get<IScorecards[]>(`${this.apiEndPoint}Get?tenantId=${tenantId}`);
  }

  getScorecardById(id: any, tenantId: number): Observable<IScorecards> {
    return this.http.get<IScorecards>(`${this.apiEndPoint}GetById?id=${id}&tenantId=${tenantId}`);
  }

  createScorecard(scoreCard: { name: string; description: string }) {
    return this.http.post(`${this.apiEndPoint}Create`, scoreCard);
  }

  updateScorecard(scoreCard: { id: number; name: string; description: string }) {
    return this.http.post(`${this.apiEndPoint}Update`, scoreCard);
  }

  deleteScorecard(id: number) {
    return this.http.post(`${this.apiEndPoint}Delete?id=${id}`, {});
  }
}
